<template>
  <div class="editor-wrapper flex flex-col">
    <label
      v-if="label && !viewOnly"
      :for="editorId"
      class="editor-label mb-spacing-100 text-body/m/relaxed/400 text-content-on-neutral-secondary"
      >{{ label }}</label
    >

    <div ref="editor" :id="editorId" class="min-h-[300px]" tabindex="0" />
  </div>
</template>

<script>
import Quill from "quill";
import "quill/dist/quill.snow.css"; // Snow theme
import ImageResize from "@taoqf/quill-image-resize-module";

Quill.register("modules/imageResize", ImageResize);

export default {
  name: "TextEditor",
  props: {
    value: {
      type: String,
      required: true,
    },
    label: {
      type: String,
      required: false,
    },
    editorId: {
      type: String,
      required: true,
    },
    viewOnly: {
      type: Boolean,
      required: false,
      default: false,
    },
  },
  data() {
    return {
      quill: null,
    };
  },
  mounted() {
    this.initQuill();
  },
  watch: {
    value(newValue) {
      // Update Quill editor content if v-model changes from outside
      if (this.quill.root.innerHTML !== newValue) {
        this.quill.root.innerHTML = newValue;
      }
    },
    viewOnly(newValue) {
      // Enable or disable the Quill editor
      this.quill.enable(!newValue);
    },
  },
  methods: {
    initQuill() {
      this.quill = new Quill(this.$refs.editor, {
        theme: "snow",
        bounds: this.$refs.editor,
        modules: {
          ...(!this.viewOnly && {
            imageResize: {
              modules: ["Resize", "DisplaySize"],
            },
          }),
          toolbar: this.viewOnly
            ? false
            : {
                container: [
                  [{ header: [1, 2, 3, 4, 5, 6, false] }],
                  ["bold", "italic", "underline", "strike"],
                  ["blockquote", "code-block"],
                  [{ list: "ordered" }, { list: "bullet" }],
                  ["link"],
                  [{ color: [] }, { background: [] }],
                  [{ align: [] }],
                  ["clean"],
                  ["image", "video"],
                ],
                // handlers: {
                //   image: this.imageHandler,
                // },
              },
        },
      });

      // Set initial content
      this.quill.root.innerHTML = this.value;

      // Set the editor to readonly mode if `viewOnly` is true
      this.quill.enable(!this.viewOnly);

      // Listen for changes and update v-model
      this.quill.on("text-change", () => {
        if (!this.viewOnly) {
          this.$emit("input", this.quill?.root?.innerHTML);
        }
      });
    },

    imageHandler() {
      let range = this.quill.getSelection();
      let value = prompt("Enter image url here.");
      if (value) {
        this.quill.insertEmbed(range.index, "image", value, Quill.sources.USER);
      }
    },
  },
};
</script>

